import React, { Component } from "react";
import { Classes } from "@blueprintjs/core";
import "./terminal.css";

import { Websocket } from "../websocket";

export class Terminal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            buffer: "",
            input: "",
            prompt: "> ",
            mask: false,
            listeners: [],
            history: [],
            hisCursor: 0,
        };
        this.input = React.createRef();
        this.container = React.createRef();
    }
    componentDidMount() {
        this.socket = new Websocket({
            uri:
                window.location.host === "gorgon.mudjs.net"
                    ? "wss://gorgon.mudjs.net/socket"
                    : "ws://localhost:5557",
            terminal: this,
        });
    }
    setMask(mask = false) {
        const state = this.state;
        state.mask = mask;
        this.setState(state);
    }
    addListener(cb) {
        const state = this.state;
        state.listeners.push(cb);
        this.setState(state);
    }
    print(data) {
        const state = this.state;
        if (state.buffer === "") {
            state.buffer = data;
        } else {
            state.buffer = `${state.buffer}\n${data}`;
        }
        this.setState(state, () => {
            this.input.current.scrollIntoView({
                behavior: "smooth",
                block: "end",
            });
        });
    }
    onClick(event) {
        this.input.current.focus();
    }
    onChange(event) {
        const state = this.state;
        state.input = event.target.value;
        this.setState(state);
    }
    onSubmit() {
        if (this.state.input !== "") {
            const inp = this.state.input;
            this.setInput();
            this.onInput(inp);
        }
    }
    onKeyPress(event) {
        const key = event.key;
        switch (key) {
            case "Enter":
                this.onSubmit();
                break;
            case "ArrowUp":
                this.onHistoryCurs(-1);
                break;
            case "ArrowDown":
                this.onHistoryCurs(1);
                break;
            case "Escape":
                this.setInput();
                break;
            default:
                break;
        }
    }
    setInput(newInput = "") {
        const state = this.state;
        state.input = newInput;
        this.setState(state, () => {
            this.input.current.focus();
        });
    }
    moveToEnd(event) {
        const temp = event.target.value;
        event.target.value = "";
        event.target.value = temp;
    }
    onHistoryCurs(dir) {
        const state = this.state;
        state.hisCursor += dir;
        if (state.hisCursor > state.history.length) state.hisCursor = 0;
        if (state.hisCursor < 0) state.hisCursor = state.history.length - 1;
        this.setInput(state.history[state.hisCursor]);
        this.setState(state);
    }
    onInput(inp) {
        if (!this.state.mask) {
            const state = this.state;
            state.history.push(inp);
            state.hisCursor = state.history.length;
            this.setState(state);
            this.print(this.state.prompt + inp);
        } else {
            this.setMask();
        }
        for (const listener of this.state.listeners) {
            listener(inp);
        }
    }
    clear() {
        const state = this.state;
        state.buffer = "";
        this.setState(state);
    }
    setPrompt(newPrompt = "> ") {
        const state = this.state;
        state.prompt = newPrompt;
        this.setState(state);
    }
    render() {
        return (
            <div
                ref={this.container}
                className="terminal-container"
                onClick={this.onClick.bind(this)}
                onKeyDown={this.onKeyPress.bind(this)}
            >
                <div className="terminal-output terminal-text">
                    {this.state.buffer}
                </div>
                <div className="terminal-input-container">
                    <span className="terminal-text terminal-prompt">
                        {this.state.prompt}
                    </span>
                    <input
                        autoFocus
                        autoComplete="new-password"
                        type={this.state.mask ? "password" : "text"}
                        ref={this.input}
                        className={[
                            "terminal-text",
                            Classes.FILL,
                            "terminal-input",
                        ].join(" ")}
                        value={this.state.input}
                        onFocus={this.moveToEnd.bind(this)}
                        onChange={this.onChange.bind(this)}
                    />
                </div>
            </div>
        );
    }
}
