export class Websocket {
    constructor(props) {
        this.socket = new WebSocket(props.uri);
        this.terminal = props.terminal;
        this.socket.onmessage = (ev) => {
            const data = ev.data;
            try {
                const dobj = JSON.parse(data);
                switch (dobj.event) {
                    case "client/prompt":
                        this.terminal.print(dobj.payload.prompt);
                        this.terminal.setMask(dobj.payload.mask);
                        break;
                    case "client/print":
                        this.terminal.print(dobj.payload);
                        break;
                    default:
                        console.log("Unhandled event from server:", dobj);
                        break;
                }
            } catch (e) {}
        };
        this.terminal.addListener((input) => {
            if (
                this.socket.readyState === 1 &&
                input !== "" &&
                input !== undefined
            ) {
                this.socket.send(input);
            }
        });
    }
    json(obj) {
        this.socket.send(JSON.stringify(obj));
    }
    event(event, payload) {
        this.json({ event, payload });
    }
    gmcp(module, data) {
        this.event("gmcp", { module, data });
    }
}
